@charset "UTF-8";
#root {
  height: 100%;
  width: 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  width: 100%;
  height: 100%;
  background-color: #eee;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.nav-container .nav-container-login-div {
  position: absolute;
  bottom: 5px;
  right: 10px; }
  .nav-container .nav-container-login-div .nav-container-login-a {
    color: white;
    font-size: 12px;
    height: 14px; }

.nav-container .nav-container-login-btn {
  background: url("./image/UserLogout.svg") no-repeat;
  background-size: 100% 100%;
  width: 12px;
  height: 12px;
  border-style: none;
  outline: none; }
  .nav-container .nav-container-login-btn:hover {
    cursor: pointer; }
    .nav-container .nav-container-login-btn:hover:after {
      position: absolute;
      right: 5px;
      top: 20px;
      padding: 5px;
      background-color: #0095ff;
      border-radius: 3px;
      color: white;
      content: "登录成功后，再次点击即可退出登录";
      font-size: 8px;
      line-height: 16px;
      z-index: 2;
      width: 100px; }

.nav {
  float: left;
  width: 80%;
  height: 100%; }

.nav ul {
  text-align: left;
  height: 100%;
  width: 100%;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  line-height: 60px;
  list-style: none;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px; }

.nav li {
  width: 80px;
  text-align: center;
  height: 57px;
  margin-left: 50px;
  display: inline-block; }

.nav a {
  color: white; }

.nav a:hover {
  font-size: 21px; }

.item-hide {
  opacity: 0;
  width: 0px;
  height: 0px;
  cursor: default;
  pointer-events: none; }

a {
  text-decoration: none; }
