@charset "UTF-8";
.calender-select-date-div {
  height: 12%;
  width: 100%;
  display: table;
  font-size: 10px; }
  .calender-select-date-div button {
    background-color: white;
    font-size: 10px;
    border: 1px; }
    .calender-select-date-div button:hover {
      cursor: pointer; }
  .calender-select-date-div .calendar-select-date-div-date-div {
    height: 100%;
    display: table-cell;
    width: 50%;
    text-align: center;
    vertical-align: middle; }
  .calender-select-date-div .calender-preMonth {
    display: table-cell;
    vertical-align: middle;
    text-align: right; }
  .calender-select-date-div .calender-nextMonth {
    display: table-cell;
    vertical-align: middle;
    text-align: left; }

.calender-month-div {
  height: 88%;
  width: 100%;
  border-top: 1px solid grey; }

.calender-div {
  border: 1px solid grey;
  margin: 10px 10px 10px 10px; }

.calender-month-table {
  table-layout: fixed;
  word-break: break-all;
  height: 100%;
  width: 100%;
  font-size: 10px; }
  .calender-month-table td {
    width: 14.28571%;
    vertical-align: middle;
    text-align: center;
    position: relative;
    cursor: default; }
    .calender-month-table td span {
      visibility: hidden;
      background-color: #9dfca9;
      color: black;
      text-align: center;
      padding: 5px 0;
      border-radius: 2px;
      /*定位*/
      position: absolute;
      z-index: 1;
      -webkit-border-radius: 2px;
      -moz-border-radius: 2px;
      -ms-border-radius: 2px;
      -o-border-radius: 2px; }
    .calender-month-table td:hover span {
      visibility: visible;
      width: 120px;
      left: 25px;
      top: -30px; }
